var fs = require("fs")
var path = require("path")

if (typeof window.__adobe_cep__ !== "undefined")
{
    /**
     *  extendScript 
     * @param script
     * @param callback
     */
    var evalScript = function (script, callback)
    {
        if (callback === null || callback === undefined)
        {
            callback = function (result) {};
        }
        window.__adobe_cep__.evalScript(script, callback);
    };

    window.evalScript = evalScript;

    /**
     *  extendScript 
     * @param script
     * @private
     */
    var _jsx = function (script)
    {
        evalScript(script, (e) => {console.log(e)})
    };
    window._jsx = _jsx;


}

/**
 * Enzymes  PhotoShop 
 * @returns {*}
 * @constructor
 */
var Enzymes = function ()
{
    //
    if (typeof window.__adobe_cep__ == "undefined")
    {
        return ""
    }


    // extendScript  Enzymes 

    if (UIDNA._ON_AUTOUPDATA_)
    {
        var aupJsxPath = path.join(setSystem._path_autoUpdateDir, "JSX_V0@" + UIDNA.verIndex)

        var readly = false;
        if (fs.existsSync(aupJsxPath))
        {
            readly = true
            readly = readly & fs.existsSync(path.join(aupJsxPath, "/JSX/json3.js"))
            readly = readly & fs.existsSync(path.join(aupJsxPath, "/JSX/Muclease_lib.jsx"))
            readly = readly & fs.existsSync(path.join(aupJsxPath, "/JSX/Kinase_lib.jsx"))
            readly = readly & fs.existsSync(path.join(aupJsxPath, "/JSX/Proteins_lib.jsx"))
        }

        if (readly)
        {
            var extendPath = aupJsxPath.replace(/\\/g, "/") //
        } else
        {
            var extendPath = cs.getSystemPath(SystemPath.EXTENSION) //
        }

    } else
    {
        var extendPath = cs.getSystemPath(SystemPath.EXTENSION) //
    }
    logger.pin("Enzymes", "Enzymes ", " Enzymes :" + extendPath)
    evalScript(`initEnzymes('${extendPath}')`)


    return this;
}


