/**
 *  TypeID
 * @param inValue  charID   "stringID"
 * @param inType  "charID"  "stringID" 4  "charID"
 * @returns {Promise}
 */
Enzymes.prototype.getTypeID = function (inValue, inType)
{
    return new Promise(function (resolve, reject)
    {
        if (inType == undefined)
        {
            if (inValue.length == 4)
            {
                inType = "charID"
            } else
            {
                inType = "stringID"
            }
        }

        if (inType == "charID")
        {
            var _jsxCode = `charIDToTypeID("${inValue}")`
            evalScript(
                _jsxCode
                , (r) => {resolve(jsxBackCheck(r, _jsxCode))})
        } else
        {
            var _jsxCode = `stringIDToTypeID("${inValue}")`
            evalScript(
                _jsxCode
                , (r) => {resolve(jsxBackCheck(r, _jsxCode))})
        }


    })


}

